////////////////                            MOBILE MENU SCSS

@media (max-width: 1170px)  {
  .menu__link{
    display: none;
  }
  .menu__link-active{
    display: block;
  }
  .menu__link-active>ul{
    display: flex;
    flex-direction: column;
  }
  .menu__link-active > ul > li{
    animation: none;
  }
  .menu{
    position: relative;
  }
  .menu__link-active{
    position: absolute;
    z-index: 10;
    bottom: -155px;
    left: 10px;
    width: 150px;
    background-color: #ffffff;
    li{
      padding: 10px;
    }
  }
  .menu_sub{
    top: 0;
  right: -125px;
    width: 125px;
    height: 222px;
  }

.menu__list2:before {
  left: -6px;
  top: 16px;}
.menu__sub2 {

  width: 119px;
  height: 210px;
  right: -113px;
}
.menu__sub2>li:before {
  top: 15px;}
}
@media (max-width: 1100px){
  .news{
    justify-content: center;
  }
}
@media (max-width: 885px){
  .brands{
    justify-content: center;
    a{
      margin: 10px;
    }
  }
}
@media (max-width: 510px){
  .navigaton{
    display: none;
  }
  .icon{
   position: static;
   margin-bottom: 0;
}
  .menu__link > ul{
    display: flex;
    justify-content: space-between;
    :hover > ul{
      display: none;
    }
  }
  .controller-wrapper {
    margin-top: 35px;}
  .slider__description h2 {
    font-size: 14px;}
  .slider__controller {
    font-size: 12px;}
  .slider__description h3 {
    margin: 3px 0 0 20px;
  }
  .slider__description p {
      font-size: 12px;
      margin-bottom: 9px;
  }
}

////////////////                            CONTACT SCSS

@media (max-width: 560px){
  .contact__page {
    align-items: center;
    flex-direction: column;}
  .contact__btn {
    margin-bottom: 50px;
    width: 100%;}
  h2{
    text-align: center;
  }
  .contacts__link div {
    margin: 15px;
  }
  .contact__info {
    width: 100%;
  }
  .contact__forms{
    width: 100%;
  }

}

////////////////                           ABOUT SCSS


@media (max-width: 980px){
  .about__me{
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 800px){
  .about__timeline{
    flex-wrap: wrap;
    justify-content: center;
  }
  .about__clients{
    display: none;
  }
}

@media (max-width: 613px){
  .about__timeline{
    flex-wrap: wrap;
  }
}
