
.header-main{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
  z-index: 10;
}
.menu{
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu__btn{
  cursor: pointer;
  margin: 10px;
  width: 50px;
  height: 50px;
  background-color: #362f2f;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}
.menu__btn-active{
  border: 1px solid #362f2f;
  background-color: #ffffff;
}

.menu__burger{
  width: 20px;
  height: 3px;
  background-color: #FFFFFF;
  position: relative;
  transition: 0.5s;
  &:before, &:after{
    content: "";
    width: 20px;
    height: 3px;
    background-color: #FFFFFF;
    position: absolute;
    transition: 1s;
  }
  &:before{
    transform: translateY(6px);
  }
  &:after{
    transform: translateY(-6px);
  }
}
.menu__burger-active{

  &:before, &:after{
    content: "";
    width: 20px;
    height: 5px;
    background-color: #362f2f;
    position: absolute;
  }
  &:before{

    transform: rotateZ(45deg);
  }
  &:after{

    transform: rotateZ(-45deg);
  }
}
.icon{
position: absolute;
  left: 50%;
  top: 0;
  margin-left: -50px;
  z-index: 99;
}
.navigaton {
  ul {
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    a {
      margin: 8px;
    }

    i {
      color: #362f2f;
    }
  }
}
.menu__link > ul > li{

  transform: translateY(-100px);
  transition: 1s;
}
.menu__link-active > ul > li{
  animation-name: animatemenu;
  animation-timing-function: linear;
  animation-duration: 3s;
  transform: translateY(0);
  opacity: 1;
 display: block;
}
@keyframes animatemenu {
  0%{
    transform: translateY(-100px);
  }
  9%{
    transform: translateY(0px);
  }
  17%{
    transform: translateY(-30px);
  }
  25%{
    transform: translateY(0px);
  }
  31%{
     transform: translateY(-20px);
   }
  40%{
    transform: translateY(0px);
  }
  49%{
     transform: translateY(-10px);
   }
  55%{
    transform: translateY(0px);
  }
  61%{
    transform: translateY(-5px);
  }
  69%{
    transform: translateY(0px);
  }
  77%{
    transform: translateY(-3px);
  }
  85%{
    transform: translateY(0px);
  }
  95%{
    transform: translateY(-1px);
  }
  100%{
      transform: translateY(0);
  }
}
.menu__link > ul{
  display: flex;
  justify-content: space-between;
  :hover > ul{
    display: flex;
  }
}
.menu a{
  font-family: "Roboto Condensed", sans-serif;
  letter-spacing: 0.1em;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  transition: all 0.5s;
  color: black;
  padding: 7px 10px;
  margin: 10px;
  &:hover, :active{
    color: #c3a000;
  }
}
.menu__list{
  position: relative;
  z-index: 999;
}
.menu_sub{
  position: absolute;
  display: none;
  flex-direction: column;
  background-color: #ffffff;
  justify-content: space-between;
  top: 25px;
  width: 112px;
  height: 145px;
  padding: 5px 0 5px 17px;
}
.menu_sub a{
  margin: 0;
  padding: 0;
}
.menu__list2{
  position: relative;
  &:before{
    content: "";
    border: 5px solid transparent; border-left: 5px solid #000000;
    position: absolute;
    left: -8px;
    top: 6px;
  }
}
.menu__sub2{
  transition: 0.5s;
  background-color: #ffffff;
  display: none;
  flex-direction: column;
  width: 113px;
  height: 113px;
  position:absolute;
  right: -113px;
  top: 0;
  padding: 6px 17px;
}
.menu__sub2 > li{
  position: relative;
  &:before{
    content: "";
    border: 5px solid transparent; border-left: 5px solid transparent;
    position: absolute;
    left: -10px;
    top: 5px;
    transition: 0.5s;
  }
  &:hover{
    &:before{
      border: 5px solid transparent; border-left: 5px solid #000000;}

  }
}
.menu__link-activeon{
  position: relative;
  &:after{
  content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #c3a000;
  }
}

