.slider{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
}
.slider__photo{
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.5s;
  opacity: 0;
  overflow: hidden;
}
.slider__photo img{
  display: block;
}
.slider__photo-active{
  opacity: 1;
  position: static;
}
.slider__controller{
  font-family: "Roboto Condensed",sans-serif;
  font-size: 14px;
  background-color: #ffffff;
  text-transform: uppercase;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: .5s;
  display: none;
}
.slider__controller-active{
  display: block;
}
.controller-wrapper{
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 20px;
}
.slider__description_wrapper{
  position: relative;

}
.slider__description{
  margin-right: 10px;
}
.slider__description h2{
  font-size: 18px;
  line-height: 7px;
}
.slider__description h3{
  text-transform: lowercase;
  font-style: italic;
  margin-bottom: 14px;
}
.slider__description p{
  font-family: "Roboto Condensed Light",sans-serif;
  font-size: 14px;
  letter-spacing: 0.001em;
  max-width: 160px;
  line-height: 14px;
  font-weight: lighter;
  text-transform: none;
  margin-bottom: 50px;
}
.slider__buttons{
  display: flex;
}
.left-btn{
  cursor: pointer;
  position: relative;
  height: 34px;
  width: 33px;
  background-color: #362f2f;
  &:before {
  content: '<';
    position: absolute;
    left: 13px;
    top: 13px;
    color: #ffffff;
    font-size: 20px;
    line-height: 0;
  }
}
.right-btn{
  cursor: pointer;
  position: relative;
  height: 34px;
  width: 33px;
  background-color: #362f2f;
  &:before{
    content: '>';
    position: absolute;
    right: 13px;
    top: 13px;
    color: #ffffff;
    font-size: 20px;
    line-height: 0;
  }
}
.slider__number{
  display: flex;
  justify-content: space-between;
  margin: 10px 18px 0 18px;
}
.sizing-arrows{
  position: absolute;
  top: 13px;
  right: 11px;
  height: 17px;
  width: 19px;
  cursor: pointer;
}
.arrow__top{
  position: absolute;
  display: block;
  width: 6px;
  height: 2px;
  background-color: #c29f00;
  transform: rotate(-45deg);
  transition: 0.5s;
  right: 0;
  &:before{
  content: "";
    border: 5px solid transparent; border-right: 5px solid #c29f00;
    position: absolute;
    top: -4px;
    left: -9px;
  }
}
.arrow__top-active{
  transform: rotate(140deg);
}
.arrow_bottom{
  position: absolute;
  display: block;
  width: 6px;
  height: 2px;
  background-color: #c29f00;
  transform: rotate(140deg);
  bottom: 2px;
  transition: 0.5s;
  left: 0;
  &:before{
    content: "";
    border: 5px solid transparent; border-right: 5px solid #c29f00;
    position: absolute;
    top: -4px;
    left: -9px;
  }
}
.arrow__bottom-active{
  transform: rotate(-45deg);
}
.slider__description_text{

}
.slider__description_text-active{
  display: none;
}


.slider__controller__icons{
  display: flex;
  justify-content: space-between;
  i{
    color: #c29f00;
  }
  margin: 0 27px 20px;
}
.slider__controller__icons-active{
   display: none;
 }

.news{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 80px;

}
.news__banner{
  max-width: 360px;
  background-color: #000000;
  color: #ffffff;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5px ;
  img{
  margin-bottom: 22px;
    height: 152px;
  }
  h2{
    margin-bottom: 9px;
    font-size: 18px;
    font-family: "Roboto Condensed Light",sans-serif;
  }
  h3{
    font-family: "Roboto Condensed",sans-serif;
    margin-bottom: 9px;
    font-size: 16px;
  }
  p{
    margin-bottom: 28px;
    font-size: 14px;
    font-family: "Roboto Condensed Light",sans-serif;
  }
  .banner__buttom{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    background-color: #c3a000;
    font-family: "Roboto Condensed",sans-serif;
    font-weight: bold;
    font-size: 14px;
    width: 67px;
    height: 67px;
    transition: .5s;
    margin-bottom: 20px;
    &:hover{
      background-color: #efcb00;
      color: #1b1b1b;
    }
  }
}
.news__links{
  max-width: 360px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px 5px;
 >h2{
   text-transform: uppercase;
   font-family: "Roboto Condensed",sans-serif;
   color: #362f2f;
   font-size: 16px;
   font-weight: bold;
   width: 50%;
   border-bottom: 1px solid #c3a000;
   margin-bottom: 20px;
 }
}
.news__link__all{
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex-grow: 1;

}
.news__link{
  display: flex;
  justify-content: space-between;
  height: 67px;
  margin: 5px 0;
  img{
    width: 67px;
    height: 67px;
  }
}
.news__link__text{
  margin-left: 30px;
  h2{
    line-height: 0.7;
    text-transform: uppercase;
    font-size: 12px;
  }
  h3{
    font-size: 11px;
    color: #7a7a7a;
  }
  p{
    font-size: 12px;
    color: #362f2f;
    a{
      font-weight: bold;
      color: #c3a000;
      text-transform: uppercase;
      margin-left: 20px;
      transition: .5s;
      &:hover{
        color: #F3D000;
      }
    }
  }
}
.news__link__text:not(:last-child){
  margin-bottom: 52px;
}
.news__contacts{
  max-width: 360px;
  margin: 5px;
}
.contacts__number, .contacts__mail{
  display: flex;
  align-items: center;
  justify-content: space-between;
  a{
    margin-left: 25px;
  }
}
.contacts__mail{
  margin-bottom: 25px;
}
.contacts__number{
 i{
  padding-left: 4px;
 }
}
.contacts__info {
  display: flex;
  justify-content: center;
  margin-bottom: 80px;

  a {
    color: #362f2f;
    display: block;
    transition: .5s;
    &:hover{
      color: #c3a000;
    }
  }

  .contacts__telephone {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 25px;
  }

  .contacts__links {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}
.contacts__quote {
  font-family: "Roboto Condensed",sans-serif;
  font-style: italic;
  font-size: 18px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  max-width: 330px;
  text-align: center;
  p{
    color: #c3a000;
    margin-top: 20px;
  }
  a{
    color: #000000;
    transition: .5s;
    &:hover{
      color: #929292;
    }
  }
}

.brands{
  display: flex;
  justify-content: space-between;
  padding: 0 18px;
  align-items: center;
  margin-bottom: 88px;
  flex-wrap: wrap;
  a{
    margin: 0 5px;
  }
}


