  }
}
#list-next{
  background-color: #362f2f;
  text-transform: uppercase;
  font-size: 12px;
  padding: 15px 25px;
  border: none;
  color: #ffffff;
  font-weight: bold;
  transition: .5s;
  &:hover{
    background-color: #000000;
  }
}
.page__number{
  width: 20px;
  height: 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  position: relative;
}
.page__number-active{
  color: #c3a000;
  &:before{
    content: '';
    position: absolute;
    left: -1px;
    top: -1px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #c3a000;
  }

}
