
*{
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  font-family: Roboto,sans-serif;
  width: 100%;
  font-size: 14px;
  background-color: #FFFFFF;
}

img {
  max-width: 100%;
  width: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  font-weight: normal; }

button {
  outline: none;
  cursor: pointer; }

a {
  cursor: pointer;
  text-decoration: none;
  color: #ffffff;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none; }
.wrapper{
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 10px;
}
.preloader{
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #000000;
  z-index: 9999;
  transition: 1s all;
  opacity: 1;
  visibility: visible;
}
.loader{
 position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation: 2s spin infinite linear;
}
.done{
  opacity: 0;
  visibility: hidden;
}

@keyframes spin {
  from{
    transform: translate(-50%, -50%) rotate(0deg);
  }
  to{
    transform: translate(-50%, -50%) rotate(360deg);
  }
}